.section.profile {
    padding-top: 2rem;
}

.profile h1 {
    font-size: 2rem;
    text-decoration: none;
    margin-bottom: 1rem;
}

.profile h1 span {
    font-size: 1rem;
}

.profile a.cv {
    text-align: center;
    display: inline-block;
    width: 100%;
    color: #FFF;
    font-weight: bold;
    cursor: pointer;
}

.profile-wrapper {
    margin-top: 3rem;
}


.profile-wrapper .profile-detail {
    position: relative;
    text-align: center;
    padding-bottom: 1px;
}

.profile-wrapper .profile-detail .overlay {
    background: url('https://c8.alamy.com/comp/PF3PC9/desktop-source-code-and-technology-background-developer-or-programer-with-coding-and-programming-wallpaper-by-computer-language-and-source-code-com-PF3PC9.jpg');
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
}

.profile-wrapper .profile-detail .profile-picture-wrapper {
    padding-top: 2rem;
    position: relative;
}

.profile-wrapper .profile-detail img {
    border-radius: 50%;
    object-fit: cover;
    max-width: 200px;
}

.profile-wrapper .profile-detail .social-media_links {
    margin: 2rem 0;
    display: flex;
    justify-content: center;
    gap: 25px;
    align-items: center;
    padding: .5rem;
}

.profile-wrapper .profile-detail ul.social-media_links li {
    cursor: pointer;
    transition: .25s;
    filter: blur(0);
}

.profile-wrapper .profile-detail ul.social-media_links li:hover{
    border: 0;
}

.profile-wrapper .profile-detail ul.social-media_links li:hover svg {
    scale: 1;
    translate: 0 0;
    transition: .25s ease-in-out;
}

.profile-wrapper .profile-detail ul.social-media_links li svg {
    scale: 0;
    translate: 100px 0;
}

.profile-wrapper .profile-detail ul.social-media_links li span {
    margin-left: .25rem;
    transform: translateY(5px);
}

.profile-wrapper .profile-description {
    text-align: justify;
    font-size: 1rem;
}

.profile-wrapper .row {
    gap: 2rem 4rem;
    margin-top: 3rem;
}

.profile-wrapper .col-lg-5 {
    background: #000;
    border-radius: 5px;
    transition: .25s background;
    margin: 1rem;
}

.profile-wrapper .col-lg-5:first-child {
    background: #fa710983;
}

.profile-wrapper .col-lg-5:hover{
    background: transparent;
    border: 1px solid #fa710983;
}

.profile-wrapper .profile-description.rotate{
    rotate: 10deg;
    background: #000;
    padding: 1rem;
    border-radius: 5px;
    transition: .25s;
    margin: 0;
}

.profile-wrapper .profile-description.rotate:hover{
    rotate: 0deg;
    background: transparent;
}

.profile-wrapper .profile-description * {
    color: #FFF;

}

.profile-wrapper .profile-description h2 {
    font-size: 1.2rem;
    margin-bottom: 1rem;
    text-decoration: none;
    font-weight: bold;
    translate: -200px 0;
    opacity: 0;
    animation: slideTitleList 1s forwards ease-in;
    animation-delay: 1s;
}

.profile-wrapper .profile-description h2 svg {
    translate: -1px -2px;
}


.profile-wrapper .profile-description ul li {
    margin: 1rem 0;
    list-style: disc;
    translate: -200px 0;
    opacity: 0;
    animation: slideList 1s forwards ease-in;
    animation-delay: 2s;
}

@keyframes slideTitleList {
    from {
        translate: -200px 0;
        opacity: 0;
    }

    to {
        translate: 0 0;
        opacity: 1;
        /* background-color: yellow; */
    }
}

@keyframes slideList {
    from {
        translate: -200px 0;
        opacity: 0;
    }

    to {
        translate: 0 0;
        opacity: 1;
    }
}